@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,500;0,700;1,100;1,300&display=swap);
body {
  --color-green-primary: #2e7d32;
  --color-green-secondary: #4A8360;
  --color-green-terciary: #84f8b281;
  --color-gray-primary: #8D8C8C;
  --color-gray-secondary: #828282;
  --color-gray-terciary: #C9C7C7;
  --color-gray-quarternary: #E0E0E0;
  --color-gray-quintenary: #FDFDFD;
  --color-base: white;
  color: var(--color-gray-primary);
  margin: 0;
  font-family: 'Roboto','Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app__routes {
  display: flex;
}

.app__routes, .app__base {
  width: 100%;
}

.app__base {
  padding-right: 35px;
  padding-bottom: 10px;
}

.card__base {
  box-shadow: 0 1px 3px var(--color-gray-terciary), 0 1px 2px var(--color-gray-terciary);
  border-radius: 10px;
  padding: 25px;
}

h1 {
  font-size: 28px;
  line-height: 32px;
  color: var(--color-gray-secondary);
}

a {
  cursor: pointer;
}

button {
  max-width: 300px;
}

.small {
  font-size: 14px;
  line-height: 22px;
}

.medium {
  font-size: 16px;
  line-height: 24px;
}

.high {
  font-size: 20px;
  line-height: 28px;
}

.btn-primary {
  background-color: var(--color-green-secondary);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    display: flex;
    padding: 0 10px;
    align-items: center;
    justify-content: space-between;
    background-color: var(--color-green-primary);
}

.header__title, .header__username {
    color: var(--color-base);
    font-weight: 400;
    margin-left: 10px;
}

.header__logo {
    width: 45px;
    height: 39px;
}

.header__group-profile, .header__group-name-project {
    display: flex;
    align-items: center;
}
.login__form {
    padding-top: 1em;
    grid-row-gap: 0.8em;
    row-gap: 0.8em;
    justify-items: center;
    display: grid;
}

.login__logo {
    width: 166px;
    height: 169px;
}

.email {
    margin: -10px 0 15px 0;
}

.login__button, .input {
    min-width: 250px !important;
}

.login__remember, .login__forgot {
    position: relative;
    display: flex;
    align-items: center;
    color: var(--color-green-primary);
}

.login__remember {
    left: -65px;
}

.login__forgot {
    left: -60px;
}
.login__signup {
    font-weight: 300;
    color: var(--color-gray-secondary);
}

.login__signup-link {
    margin-left: 10px;
    font-weight: 400;
}

.login__small {
    margin-top: 2px;
    right: -36px;
}

.login__small .remember, .login__signup-link {
    color: var(--color-green-primary);
}

.login__divisers {
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
}

.login__divisers small {
    margin: 0 10px;
}

.login__diviser--solid {
    min-width: 100px;
}

.login__forgot {
    cursor: pointer;
}
