.menu {
    margin-right: 30px;
    display: grid;
    grid-template-rows: 50px 50px 50px 50px;
    background-color: var(--color-gray-quarternary);
    color: var(--color-base);
    padding: 10px;
}
.follow-ups {
    text-align: center;
    margin-top: 25px;
    display: flex;
}

.follow-ups__top {
    display: flex;
    justify-content: end;
    margin-bottom: 20px;
}

.follow-ups__list {
    width: 80%;
}

.groups-cards {
    display: grid;
    grid-template-rows: 100px 100px 100px;
    grid-row-gap: 35px;
    row-gap: 35px;
    margin-left: 15px;
}

.emphasis {
    display: block;
    font-size: 36px;
}

.timeActual {
    font-size: 20px;
}
.card {
    background-color: var(--color-gray-quintenary);
    box-shadow: 0 1px 3px var(--color-gray-terciary), 0 1px 2px var(--color-gray-terciary);
    text-align: center;
    border-radius: 10px;
    padding: 0 15px;
    display: flex;
    width: 237px;
    flex-direction: column;
    align-items: center;
    height: 100px;
    padding-top: 18px;
}

.card__text {
    font-weight: 300;
    margin: 0;
}

.card__title-number {
    margin: 8px 0;
}
.table {
}
.search-input {
    display: flex;
    margin-left: 10px;
}

.search__button {
    text-align: center;
}
.follow-up__top {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
    width: 70%;
}

.follow-up {
    margin-top: 25px;
    padding: 25px;
    box-shadow: 0 1px 3px var(--color-gray-terciary), 0 1px 2px var(--color-gray-terciary);
    border-radius: 10px;
}

.follow-up__content {
    display: flex;
    margin-left: 10px;
    padding-top: 25px;
}
.menu-followp__link, 
.menu-followp__link-active {
  font: inherit;
  color: inherit; 
  text-decoration: inherit;
  padding: 10px;
  color: #78A78B;
  display: flex;
  align-items: center;
}

.menu-followp__link-active {
  background-color: var(--color-green-terciary);
  border-left: 3px solid #4A8360;
}

.menu-followUp {
  width: 170px;
  display: grid;
  margin-right: 10px;
  grid-template-rows: 50px 50px 50px 50px 50px 50px;
}
.containers {
    text-align: center;
    margin-top: 25px;
    display: flex;
}

.containers__top {
    display: flex;
    justify-content: end;
    margin-bottom: 20px;
}

.containers__list {
    width: 80%;
}

