}

.container__top {
    display: flex;
    justify-content: end;
    margin-bottom: 20px;
}

.container__button{
    max-width: 150px;
    display: grid;
    grid-template-rows: auto auto;
    grid-row-gap: 10px;
    row-gap: 10px;
}

.container__labels {
    width: 600px;
    display: grid;
    grid-template-columns: auto auto;
}

.timer {
    margin: 10px 50px;
}
.general { 
    display: grid;
    grid-template-columns: auto auto auto;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
}
.pagrec { 
    display: grid;
    grid-template-columns: auto auto auto;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
}
.invoice__group-input {
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    grid-row-gap: 10px;
    row-gap: 10px;
}
.packinglist {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: 56px 56px 56px;
    grid-column-gap: 50px;
    -webkit-column-gap: 50px;
            column-gap: 50px;
    grid-row-gap: 40px;
    row-gap: 40px;
}


